<template>
<div class="announcement-box">
  <div v-if="index !== 0" class="announcement-border"></div>
  <div class="content-width">
    <h5 class="text-center">{{ msgTitle }}</h5>
    {{ message }}
    <p v-if="link" class="link"><a :href="link" target="_blank">{{ link }}</a></p>
    <div v-if="imageName" class="image-frame">
      <img :src="require('../assets/' + imageName)" />
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'Announcement',
  props: ['index', 'msgTitle', 'message', 'link', 'imageName']
}
</script>

<style scoped>
.content-width {
  margin-bottom: 15px;
  padding: 10px 20px 10px 20px;
}

.announcement-border {
  border-top: 1px solid #8a0707;
}

.link {
  margin-top: 10px;
  margin-bottom: 0;
  word-wrap: break-word;
}

.image-frame {
  margin-top: 10px;
  margin-bottom: 0;
  text-align: center;
}

img {
  max-width: 100%;
  max-height: 100%;
}
</style>



