<template>
<div>
  <h3 class="page-header">Gallery</h3>
  <div class="row">
    <template v-for="name in imageNames">
      <div :key="name" class="col-12 col-sm-6 col-md-4">
        <img :src="require('../assets/' + name)" class="gallery-image"/>
      </div>
    </template>
  </div>
</div>
</template>

<script>
export default {
  name: 'Gallery',
  data: function () {
    return {
      imageNames: [
        'one.png',
        'two.png',
        'three.png',
        'four.png'
      ]
    }
  }
}
</script>

<style scoped>
.gallery-image {
  margin: 15px;
  height: 300px;
  width: 300px;
}

.page-header {
  margin-bottom: 30px;
}
</style>



