<template>
<div class="row">
  <div class="col-12 col-md-6 home">
    <div class="social-container">
      <a href="https://www.tripadvisor.com/Restaurant_Review-g47565-d4681386-Reviews-Red_Rooster_Bistro-Cutchogue_North_Fork_Long_Island_New_York.html?m=19905" target="_blank"><img src="../assets/trip-advisor.svg" class="social"/></a>
      <a href="https://www.yelp.com/biz/red-rooster-bistro-cutchogue" target="_blank" class="left-space"><img src="../assets/yelp.svg" class="social"/></a>
      <a href="https://www.facebook.com/RedRoosterBistro1/" target="_blank" class="left-space"><img src="../assets/facebook.svg" class="social"/></a>
      <a href="https://www.instagram.com/redroostercutchogue" target="_blank" class="left-space"><img src="../assets/instagram.svg" class="social"/></a>
      <a href="https://goo.gl/maps/dBWK2d3yxu1Cr6Au5" target="_blank" class="left-space"><img src="../assets/google.svg" class="social"/></a>
    </div>
    <div>
      <p class="blurb-width">Serving French-American cuisine in a relaxed country style setting.
        From simple fare like burgers, salads and fish tacos,
        to our classic Beef Bourguignon and Steak au Poivre.</p>
      <h5 class="bold margin-top">Hours</h5>
      <p>Monday: Closed</p>
      <p>Tuesday - Sunday: 12 PM - 8 PM</p>
      <h5 class="bold margin-top">Call us for take out</h5>
      <p>(631) 734-8267</p>
      <h5 class="bold margin-top">Find us here</h5>
      <p>4805 Depot Ln</p>
      <p>Cutchogue, NY 11935</p>
    </div>
  </div>
  <div class="col-12 col-md-6 announcements">
    <template v-for="(announcement, index) in announcements">
      <app-announcement
        :key="announcement.title"
        :index="index"
        :msgTitle="announcement.title"
        :message="announcement.message"
        :link="announcement.link"
        :imageName="announcement.imageName"></app-announcement>
    </template>
  </div>
  <div class="col-12 col-md-6 fb-container">
    <h3>Facebook Feed</h3>
    <div class="fb-page"
      data-href="https://www.facebook.com/RedRoosterBistro1/"
      data-tabs="timeline"
      data-width=""
      data-height="600px"
      data-small-header="false"
      data-adapt-container-width="true"
      data-hide-cover="false"
      data-show-facepile="true">
      <blockquote cite="https://www.facebook.com/RedRoosterBistro1/" class="fb-xfbml-parse-ignore">
        <a href="https://www.facebook.com/RedRoosterBistro1/">Facebook</a>
      </blockquote>
    </div>
  </div>
</div>
</template>

<script>
import Announcement from './Announcement'

export default {
  components: {
    'app-announcement': Announcement
  },
  name: 'Home',
  data: function () {
    return {
      announcements: [
        {
          title: 'Coronavirus Update',
          message: 'We are now doing take-out orders 6 days a week. Tue - Sun. ' +
            'Complimentary Bottle of wine with every $100 spent. ' +
            'Please call 631-734-8267 to place your order.'
        },
        {
          title: 'Outdoor Seating (Restrictions Permitting)',
          message: 'Check out our new "Park & Dine" area. ' +
            'Order your takeout food and beverage and enjoy' +
            'our picnic area while parked next to your private picnic table.',
          imageName: 'outdoor.jpg'
        },
        {
          title: 'Check us out on News 12',
          message: 'Red Rooster is on News 12 East End Show. Click picture to start video (3rd segment in - starts around the 10:31 mark)',
          link: 'http://www.news12.com/clip/14320836/east-end-show-for-may-5'
        }
      ]
    }
  }
}
</script>

<style scoped>
.social-container {
  margin-bottom: 20px;
}

.social {
  width: 50px;
  height: 50px;
}

@media only screen and (max-width: 600px) {
  .social {
    width: 40px;
    height: 40px;
  }
}

.margin-top {
  margin-top: 40px;
}

.blurb-width {
  max-width: 300px;
}
</style>



